/* From: Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*,
*::before,
*::after {
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
}

body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
    display: block;
    max-width: 100%;
}

input,
button,
textarea,
select {
    font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    overflow-wrap: break-word;
}

#root,
#__next {
    isolation: isolate;
}

/* -------------------------------------------------------------------------
*/

:root {
    font-size: 16px;
    --page-width: 750px;

    color: rgb(29, 29, 181);
}

header{
    display:flex;
    flex-direction: column;
    justify-content: center;
}

body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px;
    gap: 20px;
    /* height: 100vh; */
}

#eras {
    height: 40px;
}

#eras option {
    height: 40px;
}

#eras option:first-child {
    text-align: center;
}

.nav-filled {
    background-color: black;
}



@media (max-width: 425px) {
    :root {
        font-size: 12px;
    }
